import React, { Fragment, useState } from 'react'
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types'
import { connect } from 'react-redux';
import { addExperience } from '../../actions/profile';

const AddExperience = ({ addExperience, history }) => {
    const [formData, setFormData] = useState({
        company: '',
        title: '',
        location: '',
        from: '',
        to: '',
        current: false,
        description: ''
    });

    const [toDateDisabled, toggleDisabled] = useState(false);

    const {
        company,
        title,
        location,
        from,
        to,
        current,
        description
    } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value })

    return (
        <Fragment>
            <div className="container">
                <div className="row">
                    <div className="col-md-8 m-auto">
                        <h1 className="display-4 text-center">Add Experience</h1>
                        <p className="lead text-center">
                            <i className="fas fa-code-branch"></i> Add any programming/development positions you've held
                        </p>
                        <small className="d-block pb-3">* = required field</small>
                        <form onSubmit={e => {
                            e.preventDefault();
                            addExperience(formData, history);
                        }}>
                            <div className="form-group">
                                <input className="form-control" type="text" placeholder="* Job Title" name="title" required value={title} onChange={e => onChange(e)} />
                            </div>
                            <div className="form-group">
                                <input className="form-control" type="text" placeholder="* Company" name="company" required value={company} onChange={e => onChange(e)} />
                            </div>
                            <div className="form-group">
                                <input className="form-control" type="text" placeholder="Location" name="location" value={location} onChange={e => onChange(e)} />
                            </div>
                            <div className="form-group">
                                <h6>* From Date</h6>
                                <input className="form-control" type="date" name="from" value={from} onChange={e => onChange(e)} required />
                            </div>
                            <div className="form-check">
                                <p><input className="form-check-input" type="checkbox" name="current" checked={current} value={current} onChange={e => {
                                    setFormData({ ...formData, current: !current });
                                    toggleDisabled(!toDateDisabled);
                                }} />{' '}Current Job</p>
                            </div>
                            <div className="form-group">
                                <h6>To Date</h6>
                                <input className="form-control" type="date" name="to" value={to} onChange={e => onChange(e)} disabled={toDateDisabled ? 'disabled' : ''} />
                            </div>
                            <div className="form-group">
                                <textarea
                                    className="form-control"
                                    name="description"
                                    cols="30"
                                    rows="5"
                                    placeholder="Job Description"
                                    value={description} onChange={e => onChange(e)} ></textarea>
                            </div>
                            <input type="submit" className="form-control btn btn-info btn-block mt-4" />
                            <Link className="btn btn-block btn-light mb-2" to="/dashboard">Go Back</Link>
                        </form>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}

AddExperience.propTypes = {
    addExperience: PropTypes.func.isRequired
}

export default connect(null, { addExperience })(withRouter(AddExperience))
